import { Component, OnInit, Input } from '@angular/core';
import { environment } from './../../../../../environments/environment';

@Component({
  selector: 'app-product-option-select',
  templateUrl: './product-option-select.component.html',
  styleUrls: ['./product-option-select.component.scss']
})
export class ProductOptionSelectComponent implements OnInit {
  @Input() option: any;
  isClickedId: any;

  constructor() { }

  ngOnInit() {
  }
  getProductImageUrl(url) {
    var temp = url + "";
    if (temp.charAt(temp.length - 1) == '/')
      return false;
    else
      return environment.API_ENDPOINT + url;
  }

  onOptionClick(option){
    this.isClickedId = option;
  }
}



